
.cs-content{
  margin-top: 40px;
}
.table-list{
  margin-top: 20px;
}
.table-list thead tr th{
  font-size: 14px;
}
.table-list tbody tr th{
  padding: 0.65rem;
  vertical-align: middle;
  border-top: 1px solid #eceeef;
  font-size: 16px;
}
.table-list tbody tr td{
  padding: 0.65rem;
  vertical-align: middle;
  border-top: 1px solid #eceeef;
  font-size: 16px;
}
.ip-cell {
  padding-top: 15px;
}
.ip-cell p{
  line-height: 10px;
  font-size: 12px;
}
.operate-cell a:not([href]):not([tabindex]) {
  color: #2277da;
  text-decoration: none;
}
.operate-cell a{
  font-size: 14px;
  padding-top: 1px;
  padding-right: 2px;
  display: inline-block;
  vertical-align: middle;
  color: #2277da;
  text-decoration: none;
}
.list-split {
  position: absolute;
  left: 20px;
  right: 20px;
  margin: 0;
  padding: 10px 0;
  border-top: 1px solid #BEC0C3;
  z-index: 3;
}
.list-split .list-state {
  float: left;
  color: #a2a2a2;
}
.list-split .list-state span {
  font-size: 14px;
  margin-right: 10px;
}



