
.fileInfo {
  list-style: none;
  line-height: 15px;
  padding-left: 0px;
}
.fileInfo li{
  font-size: 14px;
  width: 60%;
}
.metatit{
  color: #838383;
  text-align: left;
  font-weight: bold;
}
dl dd{
  width: 350px;
  float: left;
}
.algorithm-select-list{
  margin: 0px;
  padding: 0px;
  list-style: none;
}
.algorithm-select-list li{
  float: left;
  position: relative;
  margin: 0 4px 4px 0;
  line-height: 28px;
  vertical-align: middle;
  padding: 2px;
  cursor: pointer;
}
.algorithm-select-list li a:hover{
  border: 1px solid #bd2013;
}
.algorithm-select-list li a {
  float: left;
  background-color: #fff;
  white-space: nowrap;
  width: auto!important;
  min-width: 10px;
  padding: 0 9px;
  text-align: center;
  border: 1px solid #b8b7bd;
  text-decoration: none;
}
.algorithm-select-list li .algorithm-selected{
  border: 1px solid #bd2013;
}
label {
  display: inline-block;
  margin-bottom: 0;
}



